import React, { Component } from 'react';
import logo from './images/logo_transparent.png';
import './css/App.css';
import { Navbar,  NavItem, Nav } from 'react-bootstrap';
// import GoogleMapReact from 'google-map-react';
import SimpleMap from '../src/components/SimpleMap'

class App extends Component {
  constructor(){
    super();
    this.state = {
      results: {}
    }
  }

  render() {
    return (
      <div> 
        <Navbar fixedTop collapseOnSelect>
          <Navbar.Header>
            <Navbar.Brand>
              <a>Techster Solutions</a>
            </Navbar.Brand>
            <Navbar.Toggle />
          </Navbar.Header>  
          <Navbar.Collapse>
            <Nav>
              <NavItem eventKey={1} href="#">
                Home
              </NavItem>
              <NavItem eventKey={2} href="#">
                Our Services
              </NavItem>
              <NavItem eventKey={2} href="#">
                Blog
              </NavItem>
            </Nav>
            <Nav pullRight>
              <NavItem eventKey={1} href="#">
                About Us
              </NavItem>
              <NavItem eventKey={2} href="#">
                Contact Us
              </NavItem>
            </Nav>
          </Navbar.Collapse>
        </Navbar>
        <div style={{ height: '100vh', width: '100%' }} className="App"> 
          <img src={logo} alt="logo" className="responsive"/>
          <SimpleMap></SimpleMap>
        </div>
        {/* <Navbar  collapseOnSelect>
            <Nav>
              <NavItem eventKey={1} href="#">
                © {new Date().getFullYear()} Techster Solutions. All Rights Reserved.
              </NavItem>
            </Nav>
        </Navbar> */}
      </div>
    );
  }
}

export default App;



